import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { THIS_EXPR } from '@angular/compiler/src/output/output_ast';
import { NgModel, DefaultValueAccessor } from '@angular/forms';

import * as model from '../../ikaho'

let uri: string;
uri = 'https://cors-anywhere.herokuapp.com/https://forum.ikaho.world/api'

@Component({
  selector: 'app-blog',
  templateUrl: './blog.component.html',
  styleUrls: ['./blog.component.css']
})
export class BlogComponent implements OnInit {

  pids: any = []
  topics: any = [];

  loading: boolean = true;

  constructor(private http: HttpClient) { }

  async ngOnInit() {
    await this.getPid()
  }

  async getPid(){
    let res = await this.http.get(`${uri}/recent?cid=5&cid=6`, { responseType: 'json' })
    await res.subscribe(async(data) =>{
      this.pids = [data['tids'][0],data['tids'][1],data['tids'][2]]
      for(let pid of this.pids){
        let res = await this.http.get(`${uri}/topic/${pid}`, {responseType: 'json'})
        await res.subscribe(async(data) => {
          let div = document.createElement('div')
          div.innerHTML = data['posts'][0]['content']
          let image = div.getElementsByTagName('img')[0]
          let imgSrc = image ? image.src : "assets/images/announcement.jpg";
          const topic = {
            image: imgSrc,
            title: data['title'],
            author: data['posts'][0]['user']['username'],
            content: div.textContent || div.innerText || "",
            tid: data['tid']
          }
          this.topics.push(topic)
        })
        if(this.topics.length === 3){
          this.loading = false;
        }
      }
    })
  }

}
